import { useState, useRef } from 'react'
import Axios from "axios"
import { Form, Input, Select, Button, message } from 'antd'

const { Option } = Select;

export const RsvpForm = () => {

    const [buttonLoading, setButtonLoading] = useState(false);

    const submitFunc = async (value) => {
        setButtonLoading(true);
        console.log(value)
        let notFillIn = false;
        let checkData = Object.entries(value)
        checkData.forEach(elm => {
            if (elm[1] === undefined || elm[1] === "#") notFillIn = true;
        })

        if (notFillIn) {
            message.warn("()")
            setButtonLoading(false);
            return;
        }

        try {
            message.loading("()")
            await Axios.post('https://sheet.best/api/sheets/dc8beae0-b981-4ad9-b2ba-d11ae4b8f9d2', value);
        } catch (error) {
            message.error("")
        } finally {
            message.success("(`)o")
            setButtonLoading(false);
        }
    }

    return (
        <div id="showOpacity">
            <Form
                className="info__invite-form"
                onFinish={(value) => submitFunc(value)}
            >
                <Form.Item
                    label=""
                    name="name"
                    rules={[
                        {
                            required: true,
                            pattern: /\S/,
                            message: '!',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    label=""
                    name="relation"
                    initialValue="#"
                    rules={[
                        {
                            required: true,
                            message: '!',
                        }
                    ]}
                >
                    <Select>
                        <Option value="#" disabled></Option>
                        <Option value=""></Option>
                        <Option value=""></Option>
                    </Select>
                </Form.Item>

                <Form.Item
                    label="()"
                    name="num"
                    initialValue="#"
                    rules={[
                        {
                            required: true,
                            message: '!',
                        }
                    ]}
                >
                    <Select>
                        <Option value="#" disabled></Option>
                        <Option value="1">1</Option>
                        <Option value="2">2</Option>
                        <Option value="3">3</Option>
                        <Option value="4">4</Option>
                        <Option value="5">5</Option>
                        <Option value="6">6</Option>
                        <Option value="7">7</Option>
                        <Option value="8">8</Option>
                        <Option value="9">9</Option>
                        <Option value="10">10</Option>
                    </Select>
                </Form.Item>

                <Form.Item
                    label=""
                    name="vegetarian"
                    initialValue="#"
                    rules={[
                        {
                            required: true,
                            message: '!',
                        }
                    ]}
                >
                    <Select>
                        <Option value="#" disabled></Option>
                        <Option value="0"></Option>
                        <Option value="1">1</Option>
                        <Option value="2">2</Option>
                        <Option value="3">3</Option>
                        <Option value="4">4</Option>
                        <Option value="5">5</Option>
                        <Option value="6">6</Option>
                        <Option value="7">7</Option>
                        <Option value="8">8</Option>
                        <Option value="9">9</Option>
                        <Option value="10">10</Option>
                    </Select>
                </Form.Item>

                <Form.Item>
                    <Button
                        htmlType="submit"
                        className="info__invite-button"
                        loading={buttonLoading}
                        disabled={buttonLoading}
                    ></Button>
                </Form.Item>
            </Form>
        </div>

    )
}